import { getAutoLoginFlag, getProduct, getShoppingCartItems, getAddressDetail} from 'common/js/cache'

const state = {
    isLoginPage: true,
    isAutoLogin: getAutoLoginFlag(),
    currentProduct: getProduct(),
    shoppingCartItems: getShoppingCartItems(),
    addressInfo: getAddressDetail()
}
export default state


